<template>

  <div>
    <canvas id="c" style="position: fixed;z-index: 0;width: 100%;height: 100%;"></canvas>
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app',
    mounted() {

      ;
      (function () {

        'use strict'

        var c = document.getElementById('c');
        var ctx = c.getContext('2d');
        var w = c.width = window.innerWidth;
        var h = c.height = window.innerHeight;
        var cx = w / 2;
        var cy = h / 2;
        var Box = function (x, y, vx, color) {
          this.color = color;
          this.vx = vx;
          this.x = x;
          this.y = y;
          this.w = 20 + Math.random() * 50;
          this.h = 1 + Math.random() * 200;
        };
        Box.prototype = {
          constructor: Box,
          update: function () {
            this.x += this.vx;
            if (this.x < -this.w / 2) {
              this.x = w + this.w / 2;
            }
          },
          render: function (ctx) {
            ctx.save();
            ctx.fillStyle = this.color;
            ctx.translate(this.x, this.y);
            ctx.fillRect(-this.w / 2, -this.h, this.w, this.h);
            ctx.restore();
          }
        };

        var ctr = 50;
        var boxes = [];
        var boxes2 = [];
        var boxes3 = [];
        var box;
        var speed = -1;

        for (var i = 0; i < ctr; i++) {
          box = new Box(Math.random() * w, h, speed * 0.2, '#e5e5e5');
          boxes.push(box);
        }
        for (var i = 0; i < ctr; i++) {
          box = new Box(Math.random() * w, h, speed * 0.5, '#cccccc');
          boxes2.push(box);
        }
        for (var i = 0; i < ctr; i++) {
          box = new Box(Math.random() * w, h, speed, '#aaa');
          boxes3.push(box);
        }

        requestAnimationFrame(function loop() {
          requestAnimationFrame(loop);
          ctx.clearRect(0, 0, w, h);
          ctx.globalAlpha = 0.9;
          for (var i = 0, len = boxes.length; i < len; i++) {
            box = boxes[i];
            box.update();
            box.render(ctx);
          }
          for (var i = 0, len = boxes2.length; i < len; i++) {
            box = boxes2[i];
            box.update();
            box.render(ctx);
          }
          for (var i = 0, len = boxes3.length; i < len; i++) {
            box = boxes3[i];
            box.update();
            box.render(ctx);
          }
        });

      })();
    }
  }

</script>

<style>
  @font-face {
    src: url('./assets/timeburner_regular.ttf');
    font-family: 'timebur'
  }

  body,
  html {
    width: 100%;
    height: 100%;
    /* font-family: "Helvetica Neue", Helvetica, Arial, "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif; */
    font-family: DINRegular, -apple-system, BlinkMacSystemFont, PingFang SC, Helvetica Neue, Hiragino Sans GB, Segoe UI, Microsoft YaHei, \\5FAE\8F6F\96C5\9ED1, sans-serif;
    /* background: url('./assets/bg1.png'); */
    transition: 0.3s;
    background: #e8ebef;
  }

  * {
    margin: 0px;
    padding: 0px;
  }

  p {
    margin-top: 7px;
    margin-bottom: 7px;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter,
  .fade-leave-to
  /* .fade-leave-active below version 2.1.8 */

    {
    opacity: 0;
  }

  .closebtn:hover {
    background: #3eb8ce;
  }

  .closebtn {
    position: absolute;
    width: 25px;
    height: 25px;
    background: #1d92a6;
    text-align: center;
    line-height: 18px;
    border-radius: 25px;
    color: #fff;
    border: 2px solid #fff;
    right: -13px;
    top: -13px;
    transition: 0.2s;
    cursor: pointer;
  }

  .back {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
  }

  .bg2 {
    background: url('./assets/bg16.png') no-repeat !important;
  }

  div {
    box-sizing: border-box;
    word-break: break-all;
    word-wrap: break-word;
  }

  /* *::-webkit-scrollbar {
    width: 10px;
    background-color: #eee;
  }


  *::-webkit-scrollbar-thumb {
    background-color: rgb(64, 48, 100);
  } */

  @font-face {
    font-family: qwe;
    src: url('./assets/Hyperspace.ttf');
  }

  .btn:active {
    color: #ccc;
  }

  .btn:hover {
    background: #2aafc7;
  }

  .btn {
    width: 100px;
    background: #1d92a6;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    margin: 10px 0px;
    float: left;
    border-radius: 2px;
    transition: 0.2s;
    cursor: pointer;
    user-select: none;
  }

  .yuan {
    width: 30px;
    border-radius: 30px;
  }

  .useless {
    background: #ccc !important;
    cursor: not-allowed;
  }

  /* blockquote {
    display: block;
    border-left: 8px solid #d0e5f2;
    padding: 5px 10px;
    margin: 10px 0;
    line-height: 1.4;
    font-size: 100%;
    background-color: #f1f1f1;
  }

  pre {
    background: #000 !important;
    color: #21c58a !important;
    padding: 5px !important;
    border-left: 5px solid #487751 !important;
    font-size: 14px !important;
  }

  code {
    background-color: #000 !important;
    border-radius: 0px !important;
    padding: 0px !important;
    margin: 0px !important;
  } */

  /* table 样式 */

  table {
    border-collapse: collapse;
    width: 100%;
    text-align: center;
  }

  th {
    border: 1px solid #ccc;
    padding: 3px;
  }

  td {
    border: 1px solid #ccc;
    padding: 3px;
  }

  /* blockquote 样式 */

  blockquote {
    display: block;
    border-left: 8px solid #d0e5f2;
    padding: 5px 10px;
