    line-height: 1.4;
    font-size: 100%;
    background-color: #f1f1f1;
  }

  /* code 样式 */

  code {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    background-color: #f1f1f1;
    border-radius: 3px;
    padding: 3px 5px;
    margin: 0 3px;
    font-family: 'Consolas' !important;
  }

  pre code {
    display: block;
  }

  /* ul ol 样式 */

  ul,
  ol {
    margin: 10px 0 10px 20px;
  }

  img {
    max-width: 100%;
  }

</style>



